export default {
	data() {
		return {
			userInfo: {},
			isCanUse: uni.getStorageSync('isCanUse') || false //默认为true
		};
	},
	onLoad() {
		// 默认加载
		this.wxGetUserInfo();
	},
	methods: {
		wxGetUserInfo() {
			let _this = this;
			uni.login({
				provider: 'weixin',
				success: resCode => {
					console.log('获取到code  ' + resCode.code);
					// 获取到微信的用户信息
					if (!_this.isCanUse) {
						// 第一授权获取用户信息===》按钮触发
						uni.getUserInfo({
							provider: 'weixin',
							success: res => {
								_this.userInfo = res.userInfo;
								uni.setStorageSync('userInfo', JSON.stringify(res.userInfo));
								try {
									uni.setStorageSync('isCanUse', true); //记录是否第一次授权  false:表示不是第一次授权
								} catch (e) {}
								uni.showLoading({
									title: '登录中！'
								});
								setTimeout(() => {
									uni.navigateTo({
										url: `/pages/home/home`
									});
								}, 500);
							},
							fail(res) {
								console.log(res);
							}
						});
					} else {
						uni.showLoading({
							title: '直接登录！'
						});
						setTimeout(() => {
							uni.navigateTo({
								url: `/pages/home/home`
							});
						}, 500);
					}
				},
				fail(res) {
					console.log(res);
				}
			});
		}
	}
};
